@forward 'globals';

.nav-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5em 5em 0 5em;

  &__item {
    list-style-type: none;
    display: flex;
  }

  &__links {
    padding: 0 1em 0 1em;
    display: block;
  }

  &__hamburger {
    position: absolute;
    top: 3.4em;
    right: 2em;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 30px;
    height: 21px;
  }

  @media (max-width: 414px) {
      flex-direction: column;
      align-items: flex-start;
      padding: 2em 2em 0 2em;
      display: block!important;

      &__header {
        padding: 3em 2em 0;
      }

      &__item {
        display: none;
      }

      &__hamburger {
        display: flex;
        padding: 1.6em 2em 0;
      }

      &__links {
        text-align: center;
        width: 100%;
        display: inline-block;
        flex-direction: column;
        padding: 1em 2em;
      }
  }
}

// Deals Table
.deals-table {
  padding: 2em 5em 0;
  overflow: auto;

  &__menu {
    display: flex;
    justify-content: end;
    align-items: center;
    padding: 5em 5em 0 5em;
  }

  &__menu-item {
    list-style-type: none;
    display: inline-block;
  }
  
  &__menu-links {
    padding: 0 1em 0 1em;
    display: block;
  }

  &__menu-mobile {
    display: none;
    padding: 2em 2em 0;
  }

  &__content {
    width: 100%;
  }

  &__content-title {
    padding-bottom: 1em;
  }

  &__content-company {
    width: 15em;
    color: #505676;
  }

  &__content-priorityText {
    color: #fff;
  }

  &__content-priorityTab {
    display: flex;
    justify-content: space-around;
  }

  &__content-stageText {
    color: #fff;
  }

  &__content-stageTab {
    background-color: #F4F5F7;
  }

  &__content-user-image {
    width: 40px;
    height: auto;
    clip-path: circle(30%);
    -webkit-clip-path: circle(40%);
  }

  @media (max-width: 414px) {
    &__menu {
      display: none;
    }

    &__menu-mobile {
      display: flex;
      align-items: center;
      padding: 5em 3em 1em 3em;
      justify-content: center;
    }

    &__menu-item {
      width: 50px;
    }

    &__content {
      overflow: auto;
    }

    &__content-user-image {
      width: 100%;
      height: auto;
    }
  }

}



// Closed Table
.closed-table {
  padding: 5em 5em 0 5em;
  overflow: auto;
}

.closed-table__menu {
  display: flex;
  justify-content: end;
  align-items: center;
  padding: 5em 5em 0 5em;
}

.closed-table__menu-mobile {
  display: none;
}

.closed-table__menu-item {
  list-style-type: none;
  display: inline-block;
}

.closed-table__menu-links {
  padding: 0 1em 0 1em;
  display: block;
}

.closed-table__content {
  width: 100%;
}

.closed-table__content-user-image {
  width: 45px;
  height: auto;
  clip-path: circle(40%);
  -webkit-clip-path: circle(50%);
}

@media (max-width: 820px) {
  .closed-table {
    padding: 2em 2em 0;
  }

  .closed-table__content {
    padding: 0 1em;
    overflow: auto;
  }
}

@media (max-width: 400px) {
  .closed-table {
    padding: 0 1em 0 0;
  }

  .closed-table__menu {
    display: none;
  }

  .closed-table__menu-mobile {
    display: flex;
    align-items: center;
    padding: 0 3em 0 3em;
  }

  .closed-table__menu-item {
    width: 50px;
  }

  .closed-table__content {
    /* padding: 0 3em; */
    overflow: auto;
  }

  .closed-table__content-user-image {
    width: 100%;
    height: auto;
  }
}


// Footer Table
.footer {
  padding: 5em 5em 0 5em;
}